.convexCam {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    background: linear-gradient(-45deg, rgba(0,0,0,0.12), rgba(255,255,255,0.25));
    box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.15);
    border-radius: 5px;
    border: none;
    outline: none;
    margin: auto;
}

.concaveCam {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    background: linear-gradient(135deg, rgba(0,0,0,0.22), rgba(255,255,255,0.25));
    box-shadow: 1px 1px 2px 0 rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    border: none;
    outline: none;
    margin: auto;
}